/* ========== INPUT BASE STYLES ========== */
input,
textarea,
select,
button[role="combobox"],
div[aria-readonly="true"] {
  /* Input vars */
  --input-text:             var(--text-1);
  --input-text-disabled:    var(--text-1);
  --input-face:             var(--surface-1);
  --input-face-hover:       var(--surface-hover);
  --input-face-active:      var(--surface-active);
  --input-face-disabled:    var(--surface-disabled);
  --input-border:           var(--border);
  --input-border-focus:     var(--border-focus);
  --input-outline:          var(--border-focus);
  --input-shadow:           var(--shadow-color);
  --input-border-radius:    var(--border-radius-md);

  /* Reset and base */
  width:            100%;
  border:           var(--input-border) 1px solid;
  border-radius:    var(--input-border-radius);
  background-color: var(--input-face);
  color:            var(--input-text);
  font-family:      var(--font-family-sans);
  font-size:        var(--font-size-base);
  line-height:      var(--line-height-normal);
  text-align:       left;
  outline:          none;
    
  /* Default size (medium) */
  padding: var(--spacing-2xs) var(--spacing-sm);

  /* Transitions */
  transition: all 0.2s ease;
  
  /* ========== FOCUS / ACTIVE STATES ========== */
  &:focus,
  &:focus-visible,
  &:focus-within  {
    border-color: var(--input-border-focus);
    box-shadow: 0 0 0 3px hsl(from var(--input-border-focus) h s l / 0.1);
  }
  
  /* Hover state - only when not disabled/readonly/focused */
  &:hover:not(:disabled):not(:read-only):not(:focus):not(:focus-visible):not(:focus-within) {
    background-color: var(--input-face-hover);
  }
  
  /* Active state */
  &:active:not(:disabled):not(:read-only) {
    background-color: var(--input-face-active);
    transform: translateY(1px);
  }
  
  /* ========== VALIDATION PSEUDO-CLASSES ========== */

  &:user-invalid,
  &:invalid:not(:placeholder-shown):not([type="date"]):not([type="time"]):not([type="datetime-local"]):not(select),
  &[aria-invalid="true"] {
    box-shadow: inset 0 0 8px 0 hsl(from var(--danger-face) h s l / 0.4) !important;
    
    --input-border: var(--danger-face);
    --input-border-focus: var(--danger-face);
  }
  
  /* ========== STATE ATTRIBUTES ========== */
  
  /* Disabled state */
  &:disabled {
    cursor: not-allowed;
    opacity: 0.6;
    user-select: none;
    pointer-events: none;
    box-shadow: inset 0 0 10px 0 var(--input-face-disabled);
  }
  
  /* Read-only state */
  &:read-only:not(select):not([type="checkbox"]):not([type="radio"]):not([type="button"]) {
    cursor: not-allowed;
    opacity: 0.6;
    box-shadow: inset 0 0 10px 0 var(--input-face-disabled);
  }
  
  /* ========== INPUT-SPECIFIC PSEUDO-CLASSES ========== */
  
  /* Placeholder styling - include combobox because placeholder not natively supported */
  &::placeholder,
  &[role="combobox"]:has([aria-placeholder]) {
    color: var(--input-text);
    opacity: 1;
    white-space: wrap;
    font-style: italic;
    transition: opacity 0.2s ease;
  }
  
  &::placeholder:focus {
    opacity: 0.6;
  }
  
  /* File input styling */
  &[type="file"] {
    padding: var(--spacing-md) var(--spacing-lg);
    
    &::file-selector-button {
      padding:          var(--spacing-xm) var(--spacing-md);
      margin-right:     var(--spacing-md);
      font-size:        var(--font-size-sm);
      cursor:           pointer;
      transition:       all 0.2s ease;
    }
  }
}

/* ========== INPUT SPECIFIC STYLES ========== */
input {
  /* Number inputs */
  &[type="number"] {
    appearance: textfield;
    -moz-appearance: textfield;
    
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  /* Date inputs - TODO this doesn't work */
  &[type="date"],
  &[type="time"] {
    max-width: 100%;
    width: 100%;

    @supports (-webkit-touch-callout: none) {
      padding: 0;
    }

    &::-webkit-date-and-time-value {
      text-align: start;
      padding: var(--spacing-2xs) 0 var(--spacing-2xs) var(--spacing-2xs);
    }
    &::-webkit-calendar-picker-indicator {
      filter: var(--date-picker-filter);
    }
  }
}

:root {
  --date-picker-filter: invert(30%);

  &[color-scheme='dark'] {
    --date-picker-filter: invert(80%);
  }
}

/* ========== CHECKBOX AND RADIO STYLES ========== */
input[type="checkbox"],
input[type="radio"] {
  width: 1.25rem;
  height: 1.25rem;
  min-height: auto;
  margin: 0;
  padding: 0;
  cursor: pointer;
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  background: transparent;
  border-color: var(--input-border);

  --checkbox-face: var(--blue-500);
  --checkbox-border-color: var(--link-text-hover);
  
  /* Checkbox specific */
  &[type="checkbox"] {
    border-radius: var(--border-radius-xs);
    
    &:checked,
    &:indeterminate {
      background: var(--checkbox-face);
      background-image: var(--input-icon-checkbox-checked);
      background-size: 0.75rem 0.75rem;
      background-position: center;
      background-repeat: no-repeat;
    }
    
    &:indeterminate {
      background-image: var(--input-icon-checkbox-indeterminate);
    }
  }
  
  /* Radio specific */
  &[type="radio"] {
    border-radius: 50%;
    
    &:checked {
      background: var(--checkbox-face);
      border-color: var(--checkbox-border-color);
      background-image: var(--input-icon-radio-checked);
      background-size: 0.75rem 0.75rem;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  
  /* Focus states for checkbox/radio */
  &:focus {
    box-shadow: 0 0 0 3px hsl(from var(--input-border-focus) h s l / 0.1);
  }
  
  /* Hover states for checkbox/radio */
  &:hover:not(:disabled) {
    border-color: var(--checkbox-border-color);

    &:not(:checked) {
      background-color: var(--input-face-hover);
    }
  }
  
  /* Disabled states for checkbox/radio */
  &:disabled {
    background: var(--input-face-disabled);
    border-color: var(--input-text-disabled);
    opacity: 0.5;
    cursor: not-allowed;
    
    &:checked {
      background: var(--text-4);
      border-color: var(--text-4);
      
      /* Ensure check/radio marks are visible when disabled */
      &[type="checkbox"] {
        background-image: var(--input-icon-checkbox-checked);
      }
      
      &[type="radio"] {
        background-image: var(--input-icon-radio-checked);
      }
    }
    
    &:indeterminate {
      background: var(--text-4);
      border-color: var(--text-4);
      background-image: var(--input-icon-checkbox-indeterminate);
    }
  }
}

/* ========== INPUT MODE STYLES ========== */
/* Input mode attributes */
input[inputmode="numeric"],
input[inputmode="decimal"] {
  font-variant-numeric: tabular-nums;
}

input[inputmode="url"] {
  font-family: var(--font-family-mono);
  font-size: var(--font-size-sm);
}

input[inputmode="email"] {
  text-transform: lowercase;
}

/* Pattern attributes for common patterns */
input[pattern*="[0-9]"] {
  font-variant-numeric: tabular-nums;
}

/* Required field visual indicators */
label[for] {
  cursor: pointer;
}

/* ========== TEXTAREA STYLES ========== */
textarea {
  resize: none;
  field-sizing: content;
  font-family: var(--font-family-sans);
  
  /* Override browser defaults */
  &::-webkit-scrollbar {
    width: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
    border-radius: var(--border-radius-xs);
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--border);
    border-radius: var(--border-radius-xs);
    
    &:hover {
      background: var(--border);
    }
  }
}

/* ========== SELECT STYLES ========== */
select {
  cursor: pointer;
  padding-right: 2rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
          
  &:hover:not(:disabled):not(:focus) {
    background-color: var(--input-face-hover);
  }
  
  /* Active state */
  &:active:not(:disabled) {
    background-color: var(--input-face-active);
  }

  /* Multiple select */
  &[multiple] {
    min-height: 8rem;
    background-image: none;
    padding-right: 1rem;
    
    option {
      padding: 0.5rem;
      border-radius: var(--border-radius-xs);
      margin: 0.125rem;
    }
  }
}

/* ========== ARIA relationship indicators ========== */
&[aria-describedby] {
  /* Enhanced styling for fields with descriptions */
  position: relative;
}

[aria-describedby]::after {
  content: "";
  /* Visual indicator for described fields */
}

[aria-labelledby] {
  position: relative;
}

/* ========== SIZE VARIANTS ========== */

/* Small inputs */
input.sm,
textarea.sm,
select.sm {
  padding: 0.5rem 0.75rem;
  min-height: 2.25rem;
  font-size: var(--font-size-sm);
}

/* Large inputs */
input.lg,
textarea.lg,
select.lg {
  padding: 1rem 1.25rem;
  min-height: 3.25rem;
  font-size: var(--font-size-lg);
}

/* ========== RESPONSIVE BEHAVIOR ========== */
@media (max-width: 480px) {
  input,
  textarea,
  select {
    font-size: 16px; /* Prevent zoom on iOS */
  }
}

/* ========== ACCESSIBILITY IMPROVEMENTS ========== */
@media (prefers-reduced-motion: reduce) {
  input,
  textarea,
  select {
    transition: none;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  input,
  textarea,
  select {
    border-width: 2px;
  }
  
  input[type="checkbox"],
  input[type="radio"] {
    border-width: 2px;
  }
}

/* ========== INPUT EMBEDDED ICON ========== 
  <div className="field">
    <label>Label</label>
    <div>
      <input type="text" />
      <i className="icon-class">ICON</i>
    </div>
  </div>

  We want to target all div or button elements that are direct children of .field
  and that contain an immediate input, select, or div followed by an <i> element.
*/
:is(div,button):has(> :is(input,select,div) + i) {
  position: relative;
  
  /* First child of the div or button */
  > :first-child:not(.multi-select) {
    padding-right: 2rem;
  }
  
  /* Immediate <i> element */
  > i {
    position: absolute;
    right: var(--spacing-xs);
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font-size-base);
    color: var(--text-2);
    pointer-events: none;
    width: 1.5rem;
    text-align: center;
  }
} 

