/* ========== COLOR VARIANT UTILITY CLASSES ========== */

/* PRIMARY THEME */
.primary {
  background: var(--primary-face);
  color: var(--primary-text);
  border-color: var(--primary);

  &.bold {
    background: var(--primary-face-bold);
    color: var(--primary-text-bold);
    border-color: var(--primary-face-bold);
  }

  &.subtle {
    background: var(--primary-face-subtle);
    color: var(--primary-text-subtle);
    border-color: var(--primary-face-subtle);
  }
}

/* SECONDARY THEME */
.secondary {
  background: var(--secondary-face);
  color: var(--secondary-text);
  border-color: var(--secondary);

  /* --shadow-color: var(--gray-600); */

  &.bold {
    background: var(--secondary-face-bold);
    color: var(--secondary-text-bold);
    border-color: var(--secondary-face-bold);
  }

  &.subtle {
    background: var(--secondary-face-subtle);
    color: var(--secondary-text-subtle);
    border-color: var(--secondary-face-subtle);
  }
}

/* SUCCESS THEME */
.success {
  background: var(--success-face);
  color: var(--success-text);
  border-color: var(--success);

  &.bold {
    background: var(--success-face-bold);
    color: var(--success-text-bold);
    border-color: var(--success-face-bold);
  }

  &.subtle {
    background: var(--success-face-subtle);
    color: var(--success-text-subtle);
    border-color: var(--success-face-subtle);
  }
}

/* INFO THEME */
.info {
  background: var(--info-face);
  color: var(--info-text);
  border-color: var(--info);

  &.bold {
    background: var(--info-face-bold);
    color: var(--info-text-bold);
    border-color: var(--info-face-bold);
  }

  &.subtle {
    background: var(--info-face-subtle);
    color: var(--info-text-subtle);
    border-color: var(--info-face-subtle);
  }
}

/* WARNING THEME */
.warning {
  background: var(--warning-face);
  color: var(--warning-text);
  border-color: var(--warning);

  &.bold {
    background: var(--warning-face-bold);
    color: var(--warning-text-bold);
    border-color: var(--warning-face-bold);
  }

  &.subtle {
    background: var(--warning-face-subtle);
    color: var(--warning-text-subtle);
    border-color: var(--warning-face-subtle);
  }
}

/* DANGER THEME */
.danger,
.error {
  background: var(--danger-face);
  color: var(--danger-text);
  border-color: var(--danger);

  &.bold {
    background: var(--danger-face-bold);
    color: var(--danger-text-bold);
    border-color: var(--danger-face-bold);
  }

  &.subtle {
    background: var(--danger-face-subtle);
    color: var(--danger-text-subtle);
    border-color: var(--danger-face-subtle);
  }
}

/* LIGHT THEME */
.light {
  background: var(--light-face);
  color: var(--light-text);
  border-color: var(--light);

  &.bold {
    background: var(--light-face-bold);
    color: var(--light-text-bold);
    border-color: var(--light-face-bold);
  }

  &.subtle {
    background: var(--light-face-subtle);
    color: var(--light-text-subtle);
    border-color: var(--light-face-subtle);
  }
}

/* DARK THEME */
.dark {
  background: var(--dark-face);
  color: var(--dark-text);
  border-color: var(--dark);

  &.bold {
    background: var(--dark-face-bold);
    color: var(--dark-text-bold);
    border-color: var(--dark-face-bold);
  }

  &.subtle {
    background: var(--dark-face-subtle);
    color: var(--dark-text-subtle);
    border-color: var(--dark-face-subtle);
  }
}

/* ========== GRADIENT UTILITY CLASSES ========== */

/* Main theme gradients */
.gradient.primary   { background: var(--gradient-primary) }
.gradient.secondary { background: var(--gradient-secondary) }
.gradient.success   { background: var(--gradient-success) }
.gradient.info      { background: var(--gradient-info) }
.gradient.warning   { background: var(--gradient-warning) }
.gradient.danger    { background: var(--gradient-danger) }
.gradient.dark      { background: var(--gradient-dark) }
.gradient.light     { background: var(--gradient-light) }

/* Special gradients */
.gradient.violet    { background: var(--gradient-violet) }
.gradient.sunset    { background: var(--gradient-sunset) }
.gradient.mint      { background: var(--gradient-mint) }
.gradient.rainbow   { background: var(--gradient-rainbow) }
.gradient.ocean     { background: var(--gradient-ocean) }
.gradient.fire      { background: var(--gradient-fire) }
.gradient.forest    { background: var(--gradient-forest) }
.gradient.cosmic    { background: var(--gradient-cosmic) }
