#TableCard {
  header {
    .search {
      width: 100%;
      max-width: 400px;
    }
    .hits { white-space: nowrap }
  }

  > section { 
    margin-top: 0;
  }
}

/* ModelTable table and column styles */
#ModelTable {  
  button {
    padding: 0;
    border: none;
    --button-face: transparent;
    --button-text-hover: var(--link-text-hover);
  }
  i {
    width: 1.5rem;
    text-align: center;
    color: inherit;
  }

  > thead, 
  > tbody, 
  > tfoot {
    > tr {
      border-left: 4px solid var(--table-face);

      &.search-hit {
        border-left: 4px solid var(--primary);
      }
      
      &.selected,
      &.active {
        border-left: 4px solid var(--warning);
        background-color: var(--table-face-hover);
      }

      &.error {
        border-left: 4px solid var(--danger);
      }

      &.open-item {
        border-left: 4px solid var(--warning);

        td > article {
          background: transparent;
          box-shadow: none !important;
          border: none;
        }    

        @container article (width < 600px) {
          td:first-child { display: none }
        }  
      }
    }
  }
  
  .rowmenu {
    width: 2rem;
    text-align: center;
    white-space: nowrap;
  }

  .name {
    text-align: start;

    > div:first-child {
      display: flex;

      > a {
        font-weight: var(--font-weight-base);
        text-decoration: none;
        white-space: nowrap;
      }
    }

    > :last-child {
      color: var(--text-2);
      font-size: var(--font-size-sm);

      @container article (width >= 1600px) {
        display: none
      }
    }
  }
  
  .description {
    text-align: start;

    @container article (width < 1600px) {
      display: none
    }
  }

  .sort {
    text-align: end;
    white-space: nowrap;
  }

  .key {
    white-space: nowrap;
  }

  .type,
  .code,
  .status {
    text-align: start;

    &td {
      font-size: var(--font-size-sm);
    }

    &, div { 
      white-space: nowrap;
      color: var(--text-muted);
    }
  }

  @container article (width < 600px) {
    .status,
    .type {
      display: none;
    }
  }

  @container article (width < 800px) {
    .code {
      display: none;
    }
  }
}

#ModelTable.compact {
  .key, .type, .status, .date, .sort {
    display: none;
  }
}
