.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 0;

  /* Card variables */
  --table-face:    var(--surface-1);
  --table-text:    var(--text-1);
  --table-border:  var(--border);
  --table-hover:   var(--surface-hover);
  
  /* Table base styles */
  & > :not(caption) > * > * {
    color: var(--table-text);
    background: var(--table-face);
    border: none;
  }

  /* Create a table border with rounded corners - have to target corner cells */
  border: var(--table-border) 1px solid;

  th,
  td {
    padding: 0.4rem 0.5rem;
    text-align: start;
    vertical-align: text-top;
    font-weight: var(--font-weight-medium);
  
    &.highlight {
      background: var(--table-hover);
    }
  }

  a:not(:hover) {
    color: inherit;
  }

  thead, 
  tfoot {
    vertical-align: middle;

    tr {
      border-left: 0px;
    }

    th {
      padding-top: 0.75rem;
      padding-bottom: 0.5rem;
      /* text-transform: uppercase; */
      font-weight: var(--font-weight-semibold);
      overflow-wrap: break-word;
    }

    tr.space th {
      padding-top: 1.5rem;
    }  
  }

  tbody {  
    @media screen and (hover:hover) {
      tr:hover {
        &, td, th {
          background-color: var(--table-hover);
        }
      }
    }
  }

  /* Borders */
  border-spacing: 0;
  thead {
    th,td { 
      border-top-width: 0px;
      border-bottom: 3px solid var(--table-border);
    }
  }
  tbody {
    th,td {
      border-top: 1px solid var(--table-border);
      border-bottom-width: 0px;
    }
    /* tr:first-child {
      th,td {
        border-top: 3px solid var(--table-border);
      }
    } */
  }
  tfoot {
    th,td {
      border-top: 2px solid var(--table-border);
      border-bottom-width: 0px;
    }

    &.totals td { 
      font-weight: var(--font-weight-medium) 
    }
  }

  /* Support sticky headers and footers */
  &.sticky-headers {
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      backdrop-filter: blur(10px);
    }
    tbody {
      position: sticky;
      top: 0;
      backdrop-filter: blur(10px);
    }
    tfoot {
      position: sticky;
      bottom: 0;
      backdrop-filter: blur(10px);
    }
    
    thead th:first-child,
    tbody th:first-child,
    tfoot th:first-child {
      position: sticky;
      left: 0;
      backdrop-filter: blur(10px);
    }
  }
}

table.json {
  overflow: scroll;
  
  border-top: 1px solid var(--line2);
  margin-top: 1.5rem;
  padding: 1rem 0.5rem 0 0.25rem;

  color: var(--text-muted);
  font-size: var(--font-size-xs);
  font-family: var(--font-family-mono);

  th {
    text-align: start;
    font-weight: var(--font-weight-medium);
    vertical-align: text-top;
  }
  td {
    padding-left: 1rem;
    width: 50%;
    vertical-align: text-top;
  }

  &:first-child {
    border-top: none;
    margin-top: 0;
  }

  @media (hover:hover) {
    tr:hover {
      &, td, th {
        background-color: var(--table-face-hover);
      }
    }
  }
}
