/* ========== SPACING ========== */
:root {
  --spacing-ratio: 1.25;
  --spacing-4xs: calc(var(--spacing-3xs) / var(--spacing-ratio));
  --spacing-3xs: calc(var(--spacing-2xs) / var(--spacing-ratio));
  --spacing-2xs: calc(var(--spacing-xs)  / var(--spacing-ratio));
  --spacing-xs:  calc(var(--spacing-sm)  / var(--spacing-ratio)); 
  --spacing-sm:  calc(var(--spacing-md)  / var(--spacing-ratio));
  --spacing-md:  1rem;
  --spacing-lg:  calc(var(--spacing-md)  * var(--spacing-ratio));
  --spacing-xl:  calc(var(--spacing-lg)  * var(--spacing-ratio));
  --spacing-2xl: calc(var(--spacing-xl)  * var(--spacing-ratio));
  --spacing-3xl: calc(var(--spacing-2xl) * var(--spacing-ratio));

  --border-radius-xs: 4px;  /* 0.250 rem */
  --border-radius-sm: 6px;  /* 0.375 rem */
  --border-radius-md: 8px;  /* 0.500 rem */
  --border-radius-lg: 10px; /* 0.625rem */
  --border-radius-xl: 12px; /* 0.750 rem */

  @media screen and (width < 768px) {
    --spacing-md: 0.8rem;
  }

  @media screen and (width < 500px) {
    --spacing-md: 0.7rem;
  }
}