/* ========== BUTTON BASE STYLES ========== */
button, 
a.button,
menu > a {
  /* Button vars */
  --button-text:          var(--text-1);
  --button-text-hover:    var(--text-1);
  --button-face:          var(--surface-2);
  --button-face-hover:    color-mix(in hsl, var(--button-face) 90%, var(--surface-target));
  --button-face-active:   color-mix(in hsl, var(--button-face) 80%, var(--surface-target));
  --button-face-disabled: color-mix(in hsl, var(--button-face) 80%, var(--surface-target));
  --button-border-radius: 999px;
  --button-border:        none;
  --button-outline:       var(--border);
  --button-shadow:        var(--shadow-color);

  /* Reset and base */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;

  border: var(--button-border);
  border-radius: var(--button-border-radius);
  cursor: pointer;

  line-height: 1;
  text-decoration: none;
  user-select: none;
  white-space: nowrap;
  
  /* Transitions */
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  
  /* Default size (medium) */
  padding: 0.75rem 1rem;

  /* Default theme */
  background: var(--button-face);
  color: var(--button-text);

  /* Hover state */
  &:hover:not(:disabled) {
    background: var(--button-face-hover);
    color: var(--button-text-hover);
    transform: translateY(-1px);
  }
  
  /* Active state */
  &:active:not(:disabled) {
    background: var(--button-face-active);
    transform: translateY(1px);
    filter: brightness(0.9);
  }

  /* Focus states */
  &:focus-visible {
    outline: 2px solid var(--button-outline);
    outline-offset: 2px;
  }
  
  /* Shadows use --button-shadow */
  box-shadow: 0 1px 3px hsl(from var(--button-shadow) h s l / 0.15);

  &:hover:not(:disabled) {
    box-shadow: 0 4px 12px hsl(from var(--button-shadow) h s l / 0.4);
  }
  &.active:not(:disabled) {
    box-shadow: 0 2px 4px hsl(from var(--button-shadow) h s l / 0.4);
  }

  /* Disabled state */
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none !important;
    box-shadow: none !important;
  }

  /* ========== BUTTON SIZES ========== */
  &.small {
    padding: 0.25rem 0.5rem;
    font-size: var(--font-size-sm);
  }

  &.large {
    padding: 0.75rem 1.5rem;
    font-size: var(--font-size-lg);
    min-height: 3rem;
  }

  /* ========== BUTTON VARIANTS ========== */
  &.primary {
    --button-text: var(--primary-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--primary-face);
    --button-face-hover: var(--primary-face-bold);
    --button-face-active: var(--primary-face-bold);
    --button-outline: var(--primary);
    --button-shadow: var(--primary);
  }

  &.secondary {
    --button-text: var(--secondary-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--secondary-face);
    --button-face-hover: var(--secondary-face-bold);
    --button-face-active: var(--secondary-face-bold);
    --button-outline: var(--secondary);
    --button-shadow: var(--secondary);
  }

  &.success {
    --button-text: var(--success-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--success-face);
    --button-face-hover: var(--success-face-bold);
    --button-face-active: var(--success-face-bold);
    --button-outline: var(--success);
    --button-shadow: var(--success);
  }

  &.warning {
    --button-text: var(--warning-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--warning-face);
    --button-face-hover: var(--warning-face-bold);
    --button-face-active: var(--warning-face-bold);
    --button-outline: var(--warning);
    --button-shadow: var(--warning);
  }

  &.danger {
    --button-text: var(--danger-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--danger-face);
    --button-face-hover: var(--danger-face-bold);
    --button-face-active: var(--danger-face-bold);
    --button-outline: var(--danger);
    --button-shadow: var(--danger);
  }

  &.info {
    --button-text: var(--info-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--info-face);
    --button-face-hover: var(--info-face-bold);
    --button-face-active: var(--info-face-bold);
    --button-outline: var(--info);
    --button-shadow: var(--info);
  }

  &.mint {
    --button-text: var(--mint-text);
    --button-text-hover: var(--button-text);
    --button-face: var(--mint-face);
    --button-face-hover: var(--mint-face-bold);
    --button-face-active: var(--mint-face-bold);
    --button-outline: var(--mint);
    --button-shadow: var(--mint);
  }

  /* ========== GRADIENT THEMES ========== */
  &.gradient {
    &.primary   { --button-face: var(--gradient-primary) }
    &.secondary { --button-face: var(--gradient-secondary) }
    &.success   { --button-face: var(--gradient-success) }
    &.info      { --button-face: var(--gradient-info) }
    &.warning   { --button-face: var(--gradient-warning) }
    &.danger    { --button-face: var(--gradient-danger) }
    &.light     { --button-face: var(--gradient-dark) }
    &.dark      { --button-face: var(--gradient-light) }

    &:hover:not(:disabled) {
      background: var(--button-face);
      box-shadow: 0 8px 25px hsl(from var(--button-shadow) h s l / 0.4);
      transform: translateY(-2px);
      filter: brightness(0.9);
    }

    &.active:not(:disabled) {
      box-shadow: 0 8px 25px hsl(from var(--button-shadow) h s l / 0.4);
    }
  }

  /* ========== BUTTON STATES ========== */
  &.loading {
    cursor: not-allowed;
    
    .spinner {
      display: inline-block;
      animation: spin 1s linear infinite;
    }
  }

  /* ========== Fontawesome icon support ========== */
  i {
    width: fit-content;
  }
}

/* ========== BUTTON GROUPS ========== */
.button-group {
  display: inline-flex;
  border-radius: var(--button-border-radius);
  overflow: hidden;
  
  button {
    border-right: 1px solid hsl(from var(--button-face) h s calc(l *.0.9));
    border-radius: 0;
    
    &:first-child {
      border-top-left-radius: var(--button-border-radius);
      border-bottom-left-radius: var(--button-border-radius);
    }
    
    &:last-child {
      border-top-right-radius: var(--button-border-radius);
      border-bottom-right-radius: var(--button-border-radius);
      border-right: none;
    }
  }
}

/* Contains user photo or initials */
button.avatar:has(img) {
  color: var(--button-text-hover);
  background: var(--button-face-hover);
  font-size: var(--font-size-2xs);

  padding: 0;
  margin: 0 0.5rem;
  min-width: 10px;

  &, img {
    height: 25px;
    width: 25px;
    border-radius: 50%;
  }
}


/* ========== ANIMATIONS ========== */
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
