  
.step-progress {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem;
  margin: 0;

  .step {
    height: fit-content;
    min-height: 0.5rem;
    min-width: 1rem;
    border-radius: 999px;
    border: 1px solid var(--border);
    font-size: var(--font-size-xs);
    padding: 0 0.5rem;
  }

  @container ( width < 500px ) {
    .step.success,
    .step.next {
      display: none;
    }
  }
}
