/* =========== Layout Layer - Classic Grid Layout =========== */
header {
  container: header / inline-size;
}
main {
  container: main / size;
}
article {
  container: article / inline-size;
}
footer {
  container: footer / inline-size;
}
#root {
  container: root / inline-size;
}

#root {
  display: grid;
  grid-template-areas: 
    "header header"
    "aside  main"
    "footer footer";
  grid-template-rows: auto 1fr auto;
  grid-template-columns: var(--sidebar-width) 1fr;
  max-height: 100dvh;
  min-height: 100dvh;
  gap: 0;

  background: var(--surface-0);

  /* Padding for all layout grid areas */
  --spacing-base: var(--spacing-md);
  
  > header {
    grid-area: header;
    display: flex;
    align-items: center;

    gap: var(--spacing-xs);
    padding: var(--spacing-base);

    min-width: 0;       /* Allow flex shrinking */
    overflow: hidden;

    > h1 {
      flex: 1 1 auto;
      font-size: clamp(var(--font-size-xl), 3vw, var(--font-size-4xl));
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }

    > menu {
      > button {
        padding: 0.4rem 0.5rem;
      }
    }
  }

  > aside {
    grid-area:  aside;
    overflow-y: auto;
    padding:    var(--spacing-base);
  }
  
  > main {
    grid-area:        main;
    overflow-y:       scroll;
    scrollbar-width:  thin;
    scrollbar-gutter: stable;

    > :not(form) {
      padding: 0 var(--spacing-base);
    }
  }

  > footer {
    grid-area: footer;
    display: flex;
    align-items: center;
    justify-content: center;

    gap: var(--spacing-sm);
    padding: var(--spacing-base);
  }

  /* Set sidebar width when open/closed, animate use in grid-template-columns */
  transition: grid-template-columns 300ms;

  --sidebar-width: 0px;

  aside.sidebar {
    transition: transform 300ms;
  
    &:not(.open) {
      transform: translateX(-15rem);
    }
  }

  &:has(aside.sidebar.open) {
    transform: translateX(0);
    --sidebar-width: 15rem;
  }
}

#root {
  /* Nav bars - flexible, horizontal scolling */
  nav.row {
    display: flex;
    gap: 1rem;
    overflow-x: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
    flex: 1 1 auto;
    min-width: 0;
    justify-content: flex-end;
    align-items: center;
    padding-left: 0.25rem;

    > * { flex: 0 0 auto }
    
    /* Hide scrollbar for WebKit browsers */
    &::-webkit-scrollbar {
      display: none;
    }
  }

  > header {
    a {
      text-decoration: none;
      white-space: nowrap;

      &.active {
        font-weight: var(--font-weight-semibold);
      }
    }
  }

  > aside {
    padding: 0 var(--spacing-xs);

    > div:first-child {
      font-size: var(--font-size-sm);
      font-weight: 600;
      color: var(--text-1);
      text-transform: uppercase;
      letter-spacing: 0.05em;
      margin-bottom: 1rem;
    }

    nav {
      a {
        display: block;
        padding: var(--spacing-xs) var(--spacing-xs);
        border-radius: var(--border-radius-md);
        font-size: var(--font-size-base);
        text-decoration: none;

        text-overflow: ellipsis;
        white-space: nowrap;
        overflow-x: hidden;
        
        &:hover {
          background: var(--surface-hover);
          color: var(--link-text-hover);
        }
        
        &.active {
          background: var(--surface-active);
          color: var(--link-text-hover);
          font-weight: var(--font-weight-bold);
        }

        i {
          width: 1.75rem;
          text-align: left;
        }
      }

      li {
        list-style: none;
      }
    }
  }
  
  > footer {
    font-size: var(--font-size-sm);
    color: var(--text-3);
    padding: var(--spacing-xs) var(--spacing-base);

    nav a {
      text-decoration: none;
      white-space: nowrap;
    }
  }

  > section.Toastify {
    padding: 0;
  }
}

/* Responsive Grid Design */
#root {
  @media (width < 768px) {
    
    > footer {
      align-items: top;
      gap: 1rem;

      nav.row {
        flex-direction: column;
        align-items: end;
        gap: 0.1rem;
      }

    }
  }
}

#root > main {
  /* Default grid layout which contains a collection of articles (cards) */
  > .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
    grid-auto-rows: min-content;
    gap: var(--spacing-lg);

    height: 100%;

    @container main (width < 630px) {
      grid-template-columns: 1fr;
    }
  }

  /* Center the login and loading indicators */
  &.authentication, 
  &.authentication > section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    gap: 1.5rem;
  }

  &.error-boundary {
    article {
      padding: var(--spacing-md);
      
      button {
        padding: var(--spacing-xs) var(--spacing-sm);
      }
      
      pre {
        margin: 0;
        text-wrap: nowrap;
        border: var(--border) 1px solid;
      }

      table {
        --table-face: transparent;
        --table-text: var(--danger-text);
      }
    }
  }
}

