.field .toggle {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;

  input[type="checkbox"][role="switch"] {
    flex: none;
    appearance: none;
    inline-size: 2.75rem;
    block-size: 1.5rem;
    border-radius: 999px;
    transition: background-color 150ms, border-color 150ms;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      inset-block-start: 50%;
      inset-inline-start: 0.15rem;
      transform: translateY(-50%);
      inline-size: 1.2rem;
      block-size: 1.2rem;
      border-radius: 50%;
      background: var(--input-border);
      transition: transform 150ms;
    }

    &+ span {
      font-size: var(--font-size-sm);
      color: var(--text-2);
    }

    &:checked {
      background: var(--checkbox-face);
      border-color: var(--input-border);
    }

    &:checked::after {
      transform: translate(1.25rem, -50%);
    }

    /* &:focus-visible {
      outline: 2px solid var(--interactive-primary);
      outline-offset: 2px;
    } */

    &:disabled {
      background: var(--input-face-disabled);
      border-color: var(--input-text-disabled);
      opacity: 0.5;
      cursor: not-allowed;
    }

    &:disabled + span {
      color: var(--input-text-disabled);
    }
  }

  span {
    color: var(--text-secondary);

    &::before {
      content: attr(data-off);
    }
  }

  input:checked + span::before {
    content: attr(data-on);
  }
}

@media (prefers-reduced-motion: reduce) {
  .field .toggle input[type="checkbox"][role="switch"],
  .field .toggle input[type="checkbox"][role="switch"]::after {
    transition: none;
  }
}
