.dialog {
  container: dialog / inline-size;
  width: min(45rem, calc(100% - var(--spacing-lg)));

  > form.card {
    background: var(--surface-3);
  }
}

.dialog-backdrop {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* background: rgba(255, 255, 255, 0.1); */
  backdrop-filter: blur(2px);
}
