/* ========== MENU BUTTONS ========== */
menu {
  --menu-face: transparent;
  --menu-border: none;

  display: flex;
  align-items: center;
  gap: 2px;

  background: var(--menu-face);
  border: var(--menu-border);

  > button,
  > .button,
  > div > button {
    --button-face: var(--menu-face);
    --button-border-radius: 999px;

    text-align: center;
    padding: 0.4rem 0.5rem;

    i {
      height: 1rem;
      width: 1.75rem !important;
      text-align: center;
    }


    &.active {
      background: var(--button-face-active);
    }
  }

  > .button,
  > .link {
    color: var(--button-text) !important;
  }

  > div:has(> button) {
    border: none;
    background: transparent;
    padding: 0;
    margin: 0;
  }
}

menu.segment {
  border: var(--border) 1px solid;
  border-radius: 999px;

  > button {
    padding-left: 0.75rem;
    padding-right: 0.75rem;

    &:hover:not(:disabled),
    &:active:not(:disabled) {
      transform: none;
    }
  }

  > button.active {
    background: var(--button-face-active);
  }
}

menu.popup {
  --menu-face:   var(--surface-2);
  --menu-border: var(--border) 1px solid;

  display: flex;
  flex-direction: column;
  align-items: start;

  min-width: 10rem;
  min-height: 10rem;
  width: 100%;
  gap: 0.25rem;
  padding: 0.5rem;

  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-2);

  /* Apply to all menu items (immediate children of a menu) */
  > * {
    display: block;
    width: 100%;
    padding: 0.5em;

    opacity: 1;
    text-align: left;

    > * {
      cursor: pointer;
    }

    &:not(.divider) {
      border-radius: var(--border-radius-md);
    }

    > i {
      text-align: center;
      width: 1.5em;
      margin-right: 0.5rem;
    }
  }
}
