/* Color swatches */
:root {
  /* NEUTRALS */
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);

  --gray: hsl(210, 1%, 50%);
  --gray-100: hsl(210, 17%, 98%);
  --gray-200: hsl(210, 16%, 93%);
  --gray-300: hsl(210, 14%, 89%);
  --gray-400: hsl(210, 13%, 80%);
  --gray-500: hsl(210, 11%, 71%);
  --gray-600: hsl(210,  9%, 46%);
  --gray-700: hsl(210, 11%, 31%);
  --gray-800: hsl(210, 12%, 20%);
  --gray-900: hsl(210, 14%, 13%);

  /* BLUE */
  --blue: hsl(216, 98%, 52%);
  --blue-100: hsl(214, 100%, 92%);
  --blue-200: hsl(214, 95%, 83%);
  --blue-300: hsl(214, 92%, 74%);
  --blue-400: hsl(214, 90%, 64%);
  --blue-500: hsl(216, 98%, 52%);
  --blue-600: hsl(216, 91%, 48%);
  --blue-700: hsl(216, 85%, 44%);
  --blue-800: hsl(218, 72%, 34%);
  --blue-900: hsl(218, 69%, 22%);

  /* INDIGO */
  --indigo: hsl(263, 88%, 52%);
  --indigo-100: hsl(264, 100%, 93%);
  --indigo-200: hsl(263, 89%, 85%);
  --indigo-300: hsl(263, 83%, 74%);
  --indigo-400: hsl(263, 79%, 61%);
  --indigo-500: hsl(263, 88%, 52%);
  --indigo-600: hsl(263, 82%, 46%);
  --indigo-700: hsl(263, 78%, 39%);
  --indigo-800: hsl(263, 75%, 30%);
  --indigo-900: hsl(263, 68%, 22%);

  /* PURPLE */
  --purple: hsl(266, 47%, 50%);
  --purple-100: hsl(266, 50%, 94%);
  --purple-200: hsl(266, 46%, 85%);
  --purple-300: hsl(266, 44%, 75%);
  --purple-400: hsl(266, 42%, 65%);
  --purple-500: hsl(266, 47%, 50%);
  --purple-600: hsl(266, 43%, 45%);
  --purple-700: hsl(266, 38%, 38%);
  --purple-800: hsl(266, 33%, 30%);
  --purple-900: hsl(266, 28%, 22%);

  /* PINK */
  --pink: hsl(330, 65%, 52%);
  --pink-100: hsl(330, 70%, 94%);
  --pink-200: hsl(330, 68%, 85%);
  --pink-300: hsl(330, 66%, 75%);
  --pink-400: hsl(330, 65%, 65%);
  --pink-500: hsl(330, 65%, 52%);
  --pink-600: hsl(330, 62%, 46%);
  --pink-700: hsl(330, 59%, 39%);
  --pink-800: hsl(330, 56%, 30%);
  --pink-900: hsl(330, 52%, 22%);

  /* RED */
  --red: hsl(354, 70%, 54%);
  --red-100: hsl(354, 76%, 94%);
  --red-200: hsl(354, 70%, 84%);
  --red-300: hsl(354, 66%, 74%);
  --red-400: hsl(354, 62%, 64%);
  --red-500: hsl(354, 70%, 54%);
  --red-600: hsl(354, 65%, 47%);
  --red-700: hsl(354, 60%, 36%);
  --red-800: hsl(354, 52%, 26%);
  --red-900: hsl(354, 46%, 16%);

  /* ORANGE */
  --orange: hsl(28, 98%, 54%);
  --orange-100: hsl(28, 100%, 94%);
  --orange-200: hsl(28, 97%, 84%);
  --orange-300: hsl(28, 96%, 73%);
  --orange-400: hsl(28, 95%, 62%);
  --orange-500: hsl(28, 98%, 54%);
  --orange-600: hsl(28, 89%, 46%);
  --orange-700: hsl(28, 79%, 38%);
  --orange-800: hsl(28, 69%, 30%);
  --orange-900: hsl(28, 59%, 22%);

  /* YELLOW */
  --yellow: hsl(45, 100%, 51%);
  --yellow-100: hsl(45, 100%, 94%);
  --yellow-200: hsl(45, 100%, 84%);
  --yellow-300: hsl(45, 100%, 73%);
  --yellow-400: hsl(45, 100%, 62%);
  --yellow-500: hsl(45, 100%, 51%);
  --yellow-600: hsl(45, 94%, 43%);
  --yellow-700: hsl(45, 86%, 36%);
  --yellow-800: hsl(45, 76%, 28%);
  --yellow-900: hsl(45, 66%, 20%);

  /* GREEN */
  --green: hsl(152, 49%, 41%);
  --green-100: hsl(152, 43%, 91%);
  --green-200: hsl(152, 39%, 79%);
  --green-300: hsl(152, 36%, 67%);
  --green-400: hsl(152, 38%, 55%);
  --green-500: hsl(152, 49%, 41%);
  --green-600: hsl(152, 44%, 36%);
  --green-700: hsl(152, 37%, 27%);
  --green-800: hsl(152, 30%, 18%);
  --green-900: hsl(152, 24%, 9%);

  /* TEAL */
  --teal: hsl(164, 59%, 46%);
  --teal-100: hsl(164, 57%, 93%);
  --teal-200: hsl(164, 54%, 83%);
  --teal-300: hsl(164, 51%, 72%);
  --teal-400: hsl(164, 50%, 61%);
  --teal-500: hsl(164, 59%, 46%);
  --teal-600: hsl(164, 55%, 40%);
  --teal-700: hsl(164, 49%, 31%);
  --teal-800: hsl(164, 42%, 22%);
  --teal-900: hsl(164, 35%, 13%);

  /* CYAN */
  --cyan: hsl(191, 89%, 50%);
  --cyan-100: hsl(191, 92%, 92%);
  --cyan-200: hsl(191, 89%, 82%);
  --cyan-300: hsl(191, 87%, 71%);
  --cyan-400: hsl(191, 85%, 61%);
  --cyan-500: hsl(191, 89%, 50%);
  --cyan-600: hsl(191, 80%, 43%);
  --cyan-700: hsl(191, 70%, 35%);
  --cyan-800: hsl(191, 60%, 26%);
  --cyan-900: hsl(191, 50%, 18%);
}

/* Base text color ramp */
:root {
  --text-white: var(--white);
  --text-gray-100: var(--gray-100);
  --text-gray-200: var(--gray-200);
  --text-gray-300: var(--gray-300);
  --text-gray-400: var(--gray-400);
  --text-gray-500: var(--gray-500);
  --text-gray-600: var(--gray-600);
  --text-gray-700: var(--gray-700);
  --text-gray-800: var(--gray-800);
  --text-gray-900: var(--gray-900);
  --text-black: var(--black);
}

/* Color variants */
:root {
  /* PRIMARY (blue) */
  --primary: var(--blue);
  --primary-face: var(--blue);
  --primary-text: var(--text-white);

  --primary-face-bold: var(--blue-600);
  --primary-text-bold: var(--text-white);

  --primary-face-subtle: var(--blue-300);
  --primary-text-subtle: var(--text-black);

  /* SECONDARY (gray) */
  --secondary: var(--gray-600);
  --secondary-face: var(--gray-600);
  --secondary-text: var(--text-white);

  --secondary-face-bold: var(--gray-800);
  --secondary-text-bold: var(--text-white);

  --secondary-face-subtle: var(--gray-300);
  --secondary-text-subtle: var(--text-black);

  /* SUCCESS (green) */
  --success: var(--green);
  --success-face: var(--green);
  --success-text: var(--text-white);

  --success-face-bold: var(--green-700);
  --success-text-bold: var(--text-white);

  --success-face-subtle: var(--green-300);
  --success-text-subtle: var(--text-black);

  /* INFO (cyan) */
  --info: var(--cyan);
  --info-face: var(--cyan-200);
  --info-text: var(--text-black);

  --info-face-bold: var(--cyan-700);
  --info-text-bold: var(--text-white);

  --info-face-subtle: var(--cyan-100);
  --info-text-subtle: var(--text-black);

  /* WARNING (yellow/orange) */
  --warning: var(--yellow);
  --warning-face: var(--yellow);
  --warning-text: var(--text-black);

  --warning-face-bold: var(--orange-600);
  --warning-text-bold: var(--text-white);

  --warning-face-subtle: var(--orange-400);
  --warning-text-subtle: var(--text-black);

  /* DANGER (red) */
  --danger: var(--red);
  --danger-face: var(--red);
  --danger-text: var(--text-white);

  --danger-face-bold: var(--red-600);
  --danger-text-bold: var(--text-white);

  --danger-face-subtle: var(--red-300);
  --danger-text-subtle: var(--text-white);

  /* LIGHT */
  --light: var(--gray-100);
  --light-face: var(--gray-100);
  --light-text: var(--text-black);

  --light-face-bold: var(--gray-300);
  --light-text-bold: var(--text-black);

  --light-face-subtle: var(--white);
  --light-text-subtle: var(--text-black);

  /* DARK */
  --dark: var(--gray-900);
  --dark-face: var(--gray-900);
  --dark-text: var(--text-white);

  --dark-face-bold: var(--black);
  --dark-text-bold: var(--text-white);

  --dark-face-subtle: var(--gray-700);
  --dark-text-subtle: var(--text-white);

  /* MINT (cyan) */
  --mint: var(--teal);
  --mint-face: var(--teal-300);
  --mint-text: var(--text-white);

  --mint-face-bold: var(--teal-700);
  --mint-text-bold: var(--text-white);

  --mint-face-subtle: var(--teal-100);
  --mint-text-subtle: var(--gray-600);
}

