/* =========== Glass Theme - Translucent Glass Morphism =========== */

/**
 * Glass theme provides a frosted glass effect with semi-transparent surfaces.
 * Uses backdrop-filter for blur and brightness adjustments.
 * 
 * Usage: Set color-scheme="light-glass" or "dark-glass"
 */
:root[color-scheme="light"] #root:has(main.home),
:root[color-scheme="light-glass"] {
  --surface-1: hsl(from color-mix(in hsl, var(--surface-base) 98%, var(--surface-target)) h s l / 0.10);
  --surface-2: hsl(from color-mix(in hsl, var(--surface-base) 96%, var(--surface-target)) h s l / 0.10);
  --surface-3: hsl(from color-mix(in hsl, var(--surface-base) 94%, var(--surface-target)) h s l / 0.10);
  --surface-4: hsl(from color-mix(in hsl, var(--surface-base) 92%, var(--surface-target)) h s l / 0.10);

  --border: hsl(from color-mix(in hsl, var(--surface-base) 92%, var(--surface-target)) h s l / 0.20);
}

:root[color-scheme="dark"] #root:has(main.home),
:root[color-scheme="dark-glass"] {
  --surface-1: hsl(from color-mix(in hsl, var(--surface-base) 98%, var(--surface-target)) h s l / 0.10);
  --surface-2: hsl(from color-mix(in hsl, var(--surface-base) 96%, var(--surface-target)) h s l / 0.10);
  --surface-3: hsl(from color-mix(in hsl, var(--surface-base) 94%, var(--surface-target)) h s l / 0.10);
  --surface-4: hsl(from color-mix(in hsl, var(--surface-base) 92%, var(--surface-target)) h s l / 0.10);

  --border: hsl(from color-mix(in hsl, var(--surface-base) 92%, var(--surface-target)) h s l / 0.40);
}
