/* Base Typography */
html {
  font-family: var(--font-family-sans);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-normal);
  line-height: var(--line-height-normal);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-variant-numeric: tabular-nums;

  scroll-behavior: smooth;
}

body {
  color: var(--text-1);
}

/* Heading Styles */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-family-header, var(--font-family-sans));
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-tight);
}

h1 {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-none);
  letter-spacing: var(--tracking-tight);
}

h2 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-semibold);
  letter-spacing: var(--tracking-tight);
}

h3 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
}

h4 {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-medium);
}

h5 {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
}

h6 {
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: var(--tracking-wide);
}

/* Text Elements */
p {
  line-height: var(--line-height-relaxed);
}

small {
  font-size: var(--font-size-sm);
}

strong, b {
  font-weight: var(--font-weight-semibold);
}

em, i {
  font-style: italic;
}

/* Code and Preformatted Text */
code, pre {
  font-family: var(--font-family-mono);
  font-weight: var(--font-weight-medium);
  font-size:   var(--font-size-sm);
  /* background:  var(--surface); */
}

/* code {
  color: var(--text-1);
  padding: var(--spacing-3xs) var(--spacing-2xs);
  border-radius: var(--border-radius-xs);
} */

pre {
  line-height: var(--line-height-relaxed);
  padding: var(--spacing-md);
  border-radius: var(--border-radius-md);
  overflow-x: auto;
  margin: 0;
  
  code {
    background: transparent;
    color: inherit;
    padding: 0;
    border-radius: 0;
    font-weight: var(--font-weight-normal);
  }
}

/* Interactive Text Elements */
a {
  color: var(--link-text);
  
  &:hover {
    color: var(--link-text-hover);
  }
  
  &:focus-visible {
    outline: 2px solid var(--border-focus);
    outline-offset: 2px;
    border-radius: var(--border-radius-xs);
  }
}

button {
  font-family: var(--font-family-sans);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-tight);
  letter-spacing: var(--tracking-normal);
}

/* Lists */
ul, ol {
  padding-left: var(--spacing-lg);
  
  li {
    line-height: var(--line-height-relaxed);
  }
}

/* Blockquotes */
blockquote {
  padding-left: var(--spacing-md);
  border-left: 4px solid var(--border);
  font-style: italic;
  color: var(--text-3);
  
  p {
    color: inherit;
  }
}

/* MAIN CONTENT SPACING */
main {
  /* Heading spacing */
  h1 {
    margin-top: var(--spacing-3xl);
    margin-bottom: var(--spacing-lg);
  }
  
  h2 {
    margin-top: var(--spacing-2xl);
    margin-bottom: var(--spacing-md);
  }
  
  h3 {
    margin-top: 2.5rem;
    margin-bottom: var(--spacing-xs);
  }
  
  h4 {
    margin-top: var(--spacing-xl);
    margin-bottom: var(--spacing-xs);
  }
  
  h5 {
    margin-top: var(--spacing-lg);
    margin-bottom: var(--spacing-xs);
  }
  
  h6 {
    margin-top: 1.25rem;
    margin-bottom: var(--spacing-2xs);
  }
  
  /* Paragraph spacing */
  p {
    margin-bottom: var(--spacing-xs);
  }
  p + p {
    margin-top: var(--spacing-md);
  }
  
  /* List spacing */
  ul, ol {
    margin-bottom: var(--spacing-md);
    
    li {
      margin-bottom: var(--spacing-2xs);
    }
    
    /* Nested lists */
    ul, ol {
      margin-top: var(--spacing-xs);
      margin-bottom: var(--spacing-xs);
    }
  }
  
  /* Blockquote spacing */
  blockquote {
    margin: var(--spacing-lg) 0;
  }
  
  /* Code block spacing */
  pre {
    margin: var(--spacing-lg) 0;
  }

  /* Universal typography margin reset - overrides all specific margins */
  h1, h2, h3, h4, h5, h6, p, ul, ol, li, blockquote, pre, small {
    &:first-child {
      margin-top: 0;
    }
    
    &:last-child {
      margin-bottom: 0;
    }
  }
}

/* Default cursor for anchors/links */
a, .link {
  cursor: pointer;
  text-decoration: none;
}

