/* =========== Themes Layer - Light/Dark Theme System =========== */

/* Default Light Mode Variables - other themes inherit these variables */
:root,
:root[color-scheme="light"],
:root[color-scheme="light-glass"]  {
  /* Dynamic surface variables - base configuration */
  --surface-base:   var(--white);
  --surface-target: var(--black);

  /* Explicit Surface Levels - Pre-computed for performance and simplicity */
  --surface-0: var(--surface-base);     /* Base surface (backgrounds) */
  --surface-1: color-mix(in hsl, var(--surface-base) 98%, var(--surface-target));   /* Elevated (inputs, cards) */
  --surface-2: color-mix(in hsl, var(--surface-base) 96%, var(--surface-target));   /* More elevated (buttons, menus) */
  --surface-3: color-mix(in hsl, var(--surface-base) 94%, var(--surface-target));   /* High elevation (dialogs) */
  --surface-4: color-mix(in hsl, var(--surface-base) 92%, var(--surface-target));   /* Highest elevation (tooltips, popovers) */

  /* Interactive surface states (derived from surface-1 by default) */
  --surface-hover:    color-mix(in hsl, var(--surface-2) 95%, var(--surface-target));
  --surface-active:   color-mix(in hsl, var(--surface-2) 90%, var(--surface-target));
  --surface-disabled: color-mix(in hsl, var(--surface-2) 85%, var(--surface-target));

  /* Text Variables - Content hierarchy */
  --text-1: var(--gray-900);            /* Main text color */
  --text-2: var(--gray-700);            /* Secondary text */
  --text-3: var(--gray-600);            /* Muted text */
  --text-4: var(--gray-500);            /* Disabled text */
  
  /* Border Variables - Outlines and dividers */
  --border: color-mix(in hsl, var(--surface-base) 92%, var(--surface-target));
  --border-focus: var(--blue-500);

  /* Links, anchors etc */
  --link-text:       var(--text-1);     /* Default link color */
  --link-text-hover: var(--blue-500);   /* Hover link color */
  
  /* Shadow Variables - Depth and elevation */
  --shadow-color: 0 0 0;                /* Shadow base color */
  --shadow-opacity: 0.1;                /* Shadow opacity */

  --shadow-1: 0  1px  3px hsl(var(--shadow-color) / var(--shadow-opacity));
  --shadow-2: 0  4px  6px hsl(var(--shadow-color) / calc(var(--shadow-opacity) + 0.05));
  --shadow-3: 0 10px 15px hsl(var(--shadow-color) / calc(var(--shadow-opacity) + 0.10));
  --shadow-4: 0 20px 25px hsl(var(--shadow-color) / calc(var(--shadow-opacity) + 0.15));
}

/* Explicit Dark Mode - Manual theme selection */
:root[color-scheme="dark"],
:root[color-scheme="dark-glass"] {
  /* Dynamic surface variables - base configuration */
  --surface-base:   hsl(270, 5%, 10%);
  --surface-target: var(--white);

  /* Explicit Surface Levels - Pre-computed for performance and simplicity */
  --surface-0: var(--surface-base);     /* Base surface (backgrounds) */
  --surface-1: color-mix(in hsl, var(--surface-base) 95%, var(--surface-target));  /* Elevated (inputs, cards) */
  --surface-2: color-mix(in hsl, var(--surface-base) 90%, var(--surface-target));  /* More elevated (buttons, menus) */
  --surface-3: color-mix(in hsl, var(--surface-base) 85%, var(--surface-target));  /* High elevation (dialogs) */
  --surface-4: color-mix(in hsl, var(--surface-base) 80%, var(--surface-target));  /* Highest elevation (tooltips, popovers) */

  /* Interactive surface states (derived from surface-1 by default) */
  --surface-hover:    color-mix(in hsl, var(--surface-1) 90%, var(--surface-target));
  --surface-active:   color-mix(in hsl, var(--surface-1) 85%, var(--surface-target));
  --surface-disabled: color-mix(in hsl, var(--surface-1) 87%, var(--surface-target));
  
  /* Text Variables - Light text on dark */
  --text-1: var(--white);               /* Main text color */
  --text-2: var(--gray-300);            /* Secondary text */
  --text-3: var(--gray-500);            /* Muted text */
  --text-4: var(--gray-600);            /* Disabled text */
  
  /* Border Variables - Darker borders */
  --border: color-mix(in hsl, var(--surface-base) 90%, var(--surface-target));
  --border-focus: var(--blue-400);      /* Focus rings (lighter) */
  
  /* Shadow Variables - Enhanced for dark mode */
  --shadow-color: 0 0 50;               /* White shadows */
  --shadow-opacity: 0.25;               /* Stronger shadows */
}

/* Dark Mode - System Preference */
@media screen and (prefers-color-scheme: dark) {
  :root {
    /* Dynamic surface variables - base configuration */
    --surface-base:   hsl(270, 5%, 10%);
    --surface-target: var(--white);

    /* Explicit Surface Levels - Pre-computed for performance and simplicity */
    --surface-0: var(--surface-base);     /* Base surface (backgrounds) */
    --surface-1: color-mix(in hsl, var(--surface-base) 95%, var(--surface-target));  /* Elevated (inputs, cards) */
    --surface-2: color-mix(in hsl, var(--surface-base) 90%, var(--surface-target));  /* More elevated (buttons, menus) */
    --surface-3: color-mix(in hsl, var(--surface-base) 85%, var(--surface-target));  /* High elevation (dialogs) */
    --surface-4: color-mix(in hsl, var(--surface-base) 80%, var(--surface-target));  /* Highest elevation (tooltips, popovers) */

    /* Interactive surface states (derived from surface-1 by default) */
    --surface-hover:    color-mix(in hsl, var(--surface-1) 90%, var(--surface-target));
    --surface-active:   color-mix(in hsl, var(--surface-1) 85%, var(--surface-target));
    --surface-disabled: color-mix(in hsl, var(--surface-1) 87%, var(--surface-target));
    
    /* Text Variables - Light text on dark */
    --text-1: var(--white);               /* Main text color */
    --text-2: var(--gray-300);            /* Secondary text */
    --text-3: var(--gray-500);            /* Muted text */
    --text-4: var(--gray-600);            /* Disabled text */
    
    /* Border Variables - Darker borders */
    --border: color-mix(in hsl, var(--surface-base) 85%, var(--surface-target));
    --border-focus: var(--blue-400);      /* Focus rings (lighter) */
    
    /* Shadow Variables - Enhanced for dark mode */
    --shadow-color: 0 0 50;               /* White shadows */
    --shadow-opacity: 0.25;               /* Stronger shadows */
  }
}
  
  
/* Theme Transition - Smooth color transitions */
* {
  transition: 
    background-color 200ms ease-in-out,
    border-color 200ms ease-in-out,
    color 200ms ease-in-out,
    box-shadow 200ms ease-in-out;
}

/* Disable transitions during theme switch to prevent flash */
.theme-transitioning * {
  transition: none !important;
}
