[popover] {
  margin: 0;
  inset: auto;
  border: var(--border);
  border-radius: var(--border-radius-md);
}

.anchor {
  anchor-name: var(--field-anchor, --default-field-anchor);
}

[popover]:has(menu.popup) {
  position: fixed;
  position-anchor: var(--field-anchor, --default-field-anchor);
  top: calc(anchor(bottom) + 4px);

  background: transparent;
  backdrop-filter: blur(10px) brightness(0.85);
}

.field:has([role="combobox"]) {
  [popover]:has(menu.popup) {
    left: anchor(left);
    width: anchor-size(width);
    max-width: anchor-size(width);
    position-try-fallbacks: flip-block, flip-inline;
  }
}

nav > menu {
  [popover]:has(menu.popup)  {
    justify-self: anchor-center;
  }
}

td.rowmenu {
  [popover]:has(menu.popup)  {
    position-area: left;
    position-try-fallbacks: 
      bottom left, top left;

    button {
      padding: .4rem .5rem !important;
    }
  }
}
