#CardGridView,
#SwimlaneView,
#TableCard {
  header {
    display: flex;
    h2 { 
      flex: 1 1 auto;
      text-align: start;
    }
  }
}

#CardGridView > section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: var(--spacing-md);
  margin-top: var(--spacing-md);

  h3 { 
    font-size: var(--font-size-base);
    font-weight: var(--font-weight-bold);
    text-align: start;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
  }
  h4 { 
    font-size:   var(--font-size-sm); 
    font-weight: var(--font-weight-semibold);
    
    /* Display as a lozenge */
    display: inline-block;
    padding: 2px 8px;
    margin-bottom: var(--spacing-4xs);
    background-color: var(--surface-3);
    border-radius: var(--border-radius-sm);
  }
  p, li { 
    font-size:   var(--font-size-xs);
    line-height: var(--line-height-tight);
    margin-bottom: 0.25rem;
  }
  li {
    list-style-type: "→ "
  }
  li.inbound {
    list-style-type: "← "
  }

  .item-card {
    gap: var(--spacing-sm);
    border: 1px solid var(--border);
    border-radius: var(--border-radius-md);

    color: inherit;

    header { 
      padding-bottom: 0;
      a { display: block }
    }

    section { 
      margin-top: 0;
      padding-bottom: 0;

      display: grid;
      gap: var(--spacing-sm);
      grid-template-areas:
        "description description"
        "column1     column2";

      :nth-child(1) {
        grid-area: description;
      }
      :nth-child(2) {
        grid-area: column1;
      }
      :nth-child(3) {
        grid-area: column2;
      }
    }
  }
}

/* The swimlane view is a hierarchy of ul lists, with li items having
 * nested ul when they have children.
*/
#SwimlaneView {
  ul, li {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  li {
    white-space:   wrap;
    align-items:   center;
    text-align:    center;
    height:        100%;

    > a {
      display:       block;
      text-overflow: ellipsis;
      overflow:      hidden;
      width:         100%;
      padding:       var(--spacing-4xs);

      &.vertical {
        max-height: min(150px, 100%);
        writing-mode: vertical-rl;
        text-orientation: mixed;      
      }
    }

    &:not(:has(> ul)) {
      display: flex;
      justify-content: center;

      &, > a {
        border-radius: var(--border-radius-xs);
        height: 100%;
      }
    }

    &:has(> ul) > a {
      border-top-left-radius:  var(--border-radius-xs);
      border-top-right-radius: var(--border-radius-xs);
    }
  }

  section {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(100%, 600px), 1fr));
    gap: var(--spacing-sm);
  }

  /* Level 1 items are section elements */
  li.swimlane {
    background:    var(--surface-1);
    border:        var(--border) 1px solid;
    border-radius: var(--border-radius-md);
    padding:       0 var(--spacing-xs) var(--spacing-xs) var(--spacing-xs);

    > a {
      font-size:   var(--font-size-xs);
      font-weight: var(--font-weight-bold);
      text-align:  center;
    }
  
    /* Defined grid for nested lists */
    ul {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(min(100%, 150px), 1fr));
      grid-template-rows:    auto;
      gap: var(--spacing-2xs);
    }

    /* Orphaned last item should span all columns */
    &:last-child:nth-child(odd) {
      grid-column: 1 / -1;
    }

    /* Level 2 items are li elements in this grid */
    li {
      --spacing: 4px;
  
      border:        var(--border) 1px solid;
      border-top:    var(--border) var(--border-radius-sm) solid;
      border-radius: var(--border-radius-sm);
      margin-bottom: 0;

      ul {
        padding: var(--spacing-4xs);
        gap:     var(--spacing-4xs);
      }
  
      &.level-2 {
        background:  var(--surface-2);
        font-size:   var(--font-size-2xs);
        font-weight: var(--font-weight-semibold);

        > a { line-height: 0.9rem }
      }
  
      &.level-3 { 
        display: flex;
        justify-content: center;

        background:    var(--surface-3);
        font-size:     var(--font-size-3xs); 
        font-weight:   var(--font-weight-normal);
        line-height:   var(--line-height-tight);
        border-radius: var(--border-radius-xs);
      }
  
      &.level-4 { 
        background: var(--surface-4);
      }

      /* Expands clickable area of the anchor to the parent <h2> or <h3> */
      position: relative;
      a::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }

      &:hover {
        transform: translateY(-1);
      }
    }
  
    li.success { border-top: var(--border-radius-xs) solid var(--teal-600) }
    li.warning { border-top: var(--border-radius-xs) solid var(--orange-400) }
    li.danger  { border-top: var(--border-radius-xs) solid var(--red-500) }
  }

  li.success { border-top: var(--border-radius-md) solid var(--teal-600) }
  li.warning { border-top: var(--border-radius-md) solid var(--orange-400) }
  li.danger  { border-top: var(--border-radius-md) solid var(--red-500) }
}
