/* ========== BASE CARD LAYOUT ========== */
.card,
.card-layout {
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "header"
    "content"
    "footer";
  gap: 0;
  padding: 0;

  > header { 
    grid-area: header; 
    text-align: center;
    padding: var(--spacing-sm) var(--spacing-md);
  }

  > footer { 
    grid-area: footer;
  }

  /* Content - usually a section */
  > section {
    grid-area: content;
    overflow: scroll;
    scrollbar-width: thin;
    scrollbar-gutter: stable;

    /* Spacing for all direct children. 
     * Margins top and left ensure no bleeding when scrolling with sticky headers.
     * Padding right and bottom ensure room for scrollbars.
     */
    margin:  var(--spacing-md) 0 0 var(--spacing-md);
    padding: 0 var(--spacing-md) var(--spacing-md) 0;
  }

  /* Special case for tables inside cards */
  &:has(> section > table) {
    > section {
      margin: 0;
      padding: 0;

      > table {
        border-left-width: 0px;
        border-right-width: 0px;
      }
    }
  }
}

.card-layout {
  > header {
    padding: var(--spacing-md) 0;
  }
}

.card {
  /* Card variables */
  --card-face:          var(--surface-1);
  --card-text:          var(--text-1);
  --card-border:        var(--border);
  --card-border-radius: var(--border-radius-lg);
  
  /* Card base styles */
  background:     var(--card-face);
  color:          var(--card-text);
  border:         var(--card-border) 1px solid;
  border-radius:  var(--card-border-radius);

  overflow:       hidden;
  position:       relative;
  transition:     all 0.2s ease;

  /* Layout */
  max-height: calc(100cqh - 0.25rem);

  /* Beautiful layered shadows */
  &.shadow {
    &.primary   { --shadow-color: var(--primary) }
    &.secondary { --shadow-color: var(--secondary) }
    &.success   { --shadow-color: var(--success) }
    &.info      { --shadow-color: var(--info) }
    &.warning   { --shadow-color: var(--warning) }
    &.danger    { --shadow-color: var(--danger) }
    &.light     { --shadow-color: var(--light) }
    &.dark      { --shadow-color: var(--light) }

    box-shadow: var(--shadow-1);
    &:hover {
      box-shadow: var(--shadow-2);
    }
  }

  &.link:hover {
    transform: translateY(-1px);
  }

  .tertiary {
    background: var(--surface-2);
  }

  /* ========== CARD SECTIONS ========== */
  > header {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);

    /* Reset header margins */
    h1,h2,h3 { margin: 0 }

    h2 > i {
      margin-right: 0.25rem;
    }

    > menu:last-child {
      flex: 1 1 auto;
      justify-content: end;
    }
  }

  > section {
    menu.segment {
      margin-bottom: var(--spacing-lg);
    }
  }
    
  > footer {
    display: flex;
    gap: var(--spacing-lg);

    &.right {
      justify-content: flex-end;
    }
  }

  /* ========== CARD GRIDS ========== */
  .grid {
    display: grid;
    gap: var(--spacing-lg);
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    grid-template-rows: min-content;
    
    &.compact {
      grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
      gap: var(--spacing-md);
    }
    
    &.wide {
      grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
      gap: var(--spacing-xl);
    }
  }
}

.form-view form.card,
.card.slidein {
  @container (width >= 600px) {
    width: clamp(550px, 90%, 1200px);

    &form {
      width: clamp(550px, 70%, 800px);
    }

    /* Slide up from bottom on mobile */
    animation: slideInUp 300ms ease-out forwards;
  }

  @container (width < 600px) {
    background: transparent;
    border: none;
    border-radius: 0;

    /* Slide in from right on mobile */
    animation: slideInRight 300ms ease-out forwards;
  }
}

.card-pager {
  display: flex;
  flex-direction: row;
  gap: var(--spacing-md);
  transition: 0.2s ease-in;

  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  overscroll-behavior: none;
  &::-webkit-scrollbar { display: none }

  > * {
    display: flex;
    flex-direction: column;
    flex: 0 0 calc(100cqw);
    height: 100%;

    scroll-snap-align: center;
    scroll-snap-stop: always;

    @container (width >= 800px) {
      flex: 1 1 auto;
    }
  }

  @container (width >= 600px) {
    padding: 0 var(--spacing-md);
  }

  > form.card {
    > header > h2 {
      display: none
    }

    @container (width < 600px) {
      background: transparent;
      border: none;
      border-radius: 0;
    }
  }
}
