/* ========== FORM SEMANTIC STRUCTURE ========== */
form,
form.card {
  display: flex;
  flex-direction: column;
  gap: 0;

  min-height: fit-content;
  
  /* Form Header */
  > header {
    display: block;
    padding: var(--spacing-md);
    border-bottom: none;
  }

  > :not(header):not(footer) {
    margin: 0;
    padding: var(--spacing-md);
  }
  
  /* Form Section - required for scrollable content */
  > section {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
    
    /* Section headers */
    > header {
      h1, h2, h3 {
        margin: 0 0 0.25rem 0;
        font-size: var(--font-size-lg);
        font-weight: var(--font-weight-semibold);
        color: var(--text-1);
      }
      
      p {
        margin: 0;
        color: var(--text-2);
      }
    }
  }
  
  /* Form Footer */
  > footer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--spacing-md);
    padding: var(--spacing-md);

    &:has(:nth-child(2)) {
      justify-content: space-between;
    }

    button {
      width: 15rem;
    }
  }

  p {
    font-size: var(--font-size-sm);
    text-align: center;
    text-wrap: balance;
  }

  menu.popup {
    --menu-face: var(--surface-3);
  }
}


/* ========== FIELDSET STYLES ========== */
fieldset {
  border: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
  
  legend {
    margin-bottom: 0.5rem;
    font-weight: var(--font-weight-semibold);
    font-size: var(--font-size-base);
  }

  /* Fieldset states */
  &:disabled {
    opacity: 0.6;
    pointer-events: none;
    
    legend {
      color: var(--text-4);
    }
    
    .field {
      opacity: 0.8;
    }
  }

  /* Fieldset Grid - 2 fields per row */
  &.grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }
  
  /* Fieldset Grid - 3 fields per row */
  &.grid-3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
    
    @media (min-width: 769px) and (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

/* ========== FIELD STYLES ========== */
/* Fields can have up to 2 elements - label, input, status */
.field {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2xs);
  
  /* Label comes first */
  > label {
    order:       1;
    line-height: var(--line-height-tight);
    font-weight: var(--font-weight-medium);
    font-size:   var(--font-size-sm);
    color:       var(--text-2);
    text-align:  start;
  }
  
  /* Input control comes second */
  > input,
  > textarea,
  > select,
  > button,
  > div {
    order:           2;
    text-align:      start;
    justify-content: start;
    max-width:       100%;
  }

  /* Help and status messages come third */
  .help,
  .success,
  .warning,
  .error {
    order:        3;
    display:      flex;
    align-items:  center;
    gap:          0.375rem;
    /* padding-left: var(--spacing-md); */
    font-size:    var(--font-size-sm);
    background:   inherit !important;
  }
  
  .help    { color: var(--text-3) }
  .success { color: var(--success) }
  .warning { color: var(--warning) }
  .error   { color: var(--danger) }
  
  /* Special handling for checkbox/radio fields */
  &:has(input[type="checkbox"]),
  &:has(input[type="radio"]) {
    display:        flex;
    flex-direction: row;
    align-items:    center;
    gap:            0.5rem;
    cursor:         pointer;

    &:has(> input) {
      input { order: 1 }
      label { order: 2 }
    }

    div:has(input) {
      display: flex;
      align-items: center;
      gap: 0.5rem;

      input { order: 1 }
      span  { order: 2 }
    }
  }
  
  /* Field contains disabled input */
  &:has(:disabled) {
    label { cursor: not-allowed }
  }

  /* Required field indicator */
  &.required,
  &:has(:required) {
    label:after {
      content: "*";
    }
  }
}

/* ========== FORM RESPONSIVE DESIGN ========== */
#root {
  .form-view {
    display: flex;
    justify-content: center;

    &header {
      h1 { text-align: center; }
    }
  
    &main {
      form,
      form.card {
        > header > h2 {
          display: none
        }
      }
    }
  }
}

/* Apple style Grid - 1 field per row */
@container (width > 10rem) {
  form.apple {
    > section {
      /* Override the base flex layout with grid for apple forms */
      display: grid;
      grid-template-columns: clamp(5rem, 25%, 15rem) minmax(min(15rem, 100%), 1fr);
      gap: var(--spacing-md);
      align-items: start;

      /* Section headers need to span the full grid */
      > header {
        grid-column: 1 / -1;
      }

      > fieldset {
        /* Fieldset spans full width and creates its own grid matching the parent */
        display: grid;
        grid-template-columns: clamp(5rem, 25%, 15rem) 1fr;
        grid-column: span 2;
        gap: var(--spacing-md);
        padding: var(--spacing-sm) var(--spacing-md);

        background: var(--surface-2);
        border: 1px solid var(--border);
        border-radius: var(--border-radius-lg);

        .field {
          /* Each field spans both columns within the fieldset */
          display: grid;
          grid-template-columns: subgrid;
          grid-column: span 2;
          align-items: center;
          gap: 0.25rem var(--spacing-md);

          .help,
          .success,
          .warning,
          .error {
            grid-column: 2 / 3;
          }
        }
      }
    }
  }
}

/* ========== ACCESSIBILITY ========== */
.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

